.Home {
    color: white;
    min-height: 100vh;
    padding: 10% 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__button {
        width: 60%;
        padding: 15px 0;
        border: none;
        background-color: rgb(199, 199, 199);
        font-size: 18px;
        border-radius: 50px;
        outline: none;
        text-align: center;
        box-shadow: 0 1px 8px rgba(128, 128, 128, 0.377);
    }
}